import React from "react"
import addToMailchimp from 'gatsby-plugin-mailchimp'
import ReactModal from 'react-modal'
import EssentialsImage from "./images/essentials"

class NewsletterPDF extends React.Component {
    _handleChange = (e) => {
        this.setState({
            [`${e.target.name}`]: e.target.value,
        });
    }

    _handleSubmit = (e) => {
        e.preventDefault();

        var self = this;
        addToMailchimp(this.state.email, this.state)
            .then(({ msg, result }) => {
                if (result !== 'success') {
                    throw msg;
                }

                self.setState({
                    email: '',
                    showMessage: true,
                    message: msg + "<br />" + "An email with the Magento 2 Essentials Cheat Sheet has been sent to your inbox.",
                    messageClassName: 'subscription text-center alert alert-success'
                });
            })
            .catch((err) => {
                self.setState({
                    showMessage: true,
                    message: err,
                    messageClassName: 'subscription text-center alert alert-warning'
                });
            });
    }

    constructor(props) {
        super(props)
        this.state = {
            isModalOpen: false,
            email: '',
            showMessage: false,
            messageClassName: '',
            PDF_FILE: 'magento-2-essentials.pdf'
        }
    }
    handleModalOpen = event => {
        this.setState({ isModalOpen: true })
    }

    handleModalClose = event => {
        this.setState({ isModalOpen: false })
    }

    render() {
        return (
            <div className="mt-5 mb-0">
                <div className="row">
                    <div className="col-md-4">
                        <div className="essentials-image-container mx-auto">
                            <EssentialsImage/>
                        </div>
                    </div>
                    <div className="col-md-8">
                        <div className="essentials">
                            <h3>Get the Ultimate Magento 2 Essentials Cheat Sheet</h3>
                            <p className="lead">A practical Magento 2 PDF with PHP code samples at your fingertips.</p>
                            <a href="#"
                               className="btn btn-main-download mt-5 pt-2 pb-2 pl-3 pr-3" role="button" aria-pressed="true"
                               onClick={this.handleModalOpen}>
                                <div className="button-label">
                                    <span className="label-text">Download for Free</span>
                                </div>
                            </a>

                            <ReactModal
                              isOpen={this.state.isModalOpen}
                              onRequestClose={this.handleModalClose}
                              contentLabel="Magento 2 Essentials Cheat Sheet"
                              shouldFocusAfterRender={false}
                            >
                                <div className="modal-container">
                                    <h4 className="modal-header">Please subscribe to download free</h4>
                                    <div className="jumbotron jumbotron-fluid p-3">
                                        <div className="">
                                            <p className="lead">10 pages on how to write Magento 2 code.</p>
                                            <p className="lead">Sign up and receive the Magento 2 Essentials Cheat Sheet directly into your inbox.</p>
                                            <div className="mt-4">
                                                { this.state.showMessage && <div className={this.state.messageClassName} dangerouslySetInnerHTML={{ __html: this.state.message}} />}
                                                <div className="mx-auto form-container">
                                                    <form onSubmit={this._handleSubmit}>
                                                        <div className="input-group mb-3 subscribe-input-group">
                                                            <input type="email"
                                                                   name="email"
                                                                   onChange={this._handleChange}
                                                                   className="form-control"
                                                                   aria-describedby="subscribe"
                                                                   placeholder="Your email here..."
                                                                   value={this.state.email}
                                                            />
                                                            <div className="input-group-append">
                                                                <button className="btn btn-outline-secondary" type="button" id="subscribe" onClick={this._handleSubmit}>Subscribe</button>
                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </ReactModal>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default NewsletterPDF
